$purpleLight: rgb(172, 9, 172);
$purpleDark: rgb(49, 3, 49);

* {
    box-sizing: border-box;
}

body{
    background-color: #8EC5FC;
    background-image: linear-gradient(62deg, #8EC5FC 0%, #E0C3FC 100%);

}

#side-menu{
    background: $purpleLight;
    width: 280px;
    position: absolute;
    top:0;
    bottom:0;
    height: 100vh;

    nav{
        padding-top: 100px;
        a{
            display: block;
            color: white;
            text-decoration: none;
            padding: 15px 30px;

            .fa {
                color: white;
                font-size: size 1.4rem;
                margin-right: 0.8rem;
                width:30px;
                position:relative;
            }

            &:hover{
                transition: all .3s ease-in-out;
                background:$purpleDark;
                border-left: 5px solid greenyellow;
                padding: 15px 30px 15px 25px;

                .fa {
                    color: greenyellow;
                }
            }
            &.active{
                background:$purpleDark;
                border-left: 5px solid greenyellow;
                padding: 15px 30px 15px 25px;

                .fa {
                    color: greenyellow;
                }
            }

        }
    }
}

#header{
    padding-left: 280px;
}